import { Component, OnInit, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { CountriesService } from 'app/shared/services/countries.service';
import { DatePipe } from '@angular/common';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { EVDealersService } from 'app/shared/services/ev-space/ev-dealers/EV-dealers.service';
import { MatDialog } from '@angular/material/dialog';
import { MapDialogComponent } from 'app/shared/components/map-dialog/map-dialog.component';
import { RegisterChargerRequestService } from 'app/shared/services/customer-management/register-charger-request.service';
import { MasterService } from 'app/shared/services/master/master.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}


@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})


export class CreateComponent implements OnInit {

  form: FormGroup;
  localStorage: any;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries: any;
  states: any;
  stateName: any;
  cities: any;
  pin_code_id: any;
  country_id: any;
  state_id: any;
  @ViewChild("toggleElement") ref: any;
  status = true;
  mLat: any;
  mLong: any;
  connectorTypes: any;
  filteredOptionsConnectorType: Observable<any[]>;
  chargerData: any;
  flag: boolean = true;

  constructor(private fb: FormBuilder, public countryService: CountriesService, private regCharReqService: RegisterChargerRequestService,
    private snackBar: MatSnackBar, private router: Router, private datePipe: DatePipe, public dialog: MatDialog,
    private masterService: MasterService,) {

  }

  ngOnInit() {
    this.initialize();
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.form = this.fb.group({
      station_name: ['', [Validators.required,]],
      provider: ['', [Validators.required,]],
      model: ['', [Validators.required,]],
      lat: ['', [Validators.required,]],
      lng: ['', Validators.required],
      char_seri_no: ['', Validators.required],
      addressLine1: [''],
      addressLine2: [''],
      pincode: ['', [Validators.required, Validators.pattern('^[1-9]{1}[0-9]{2}\\s{0,1}[0-9]{3}$')]],
      city: ['', [autocompleteObjectValidator(), Validators.required]],
      state: ['', [autocompleteObjectValidator(), Validators.required]],
      country: ['', [autocompleteObjectValidator(), Validators.required]],
      landmark: [''],
      remarks: [''],
      image: [''],
      status: this.status == true ? 'M' : 'N'
    });

    this.getAllCountry();
    this.getAllState();
    this.getConnectorTypes();
    //this.getAllCity();
  }

  getAllCountry() {
    this.countryService.getAllCountry().subscribe(
      (resp: any) => {
        this.countries = resp;
        this.filteredOptionCountry = this.form.controls.country.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCountry(name) : this.countries)
          );
      }, (err) => {

      }
    )
  }
  displayCountry(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getAllState() {
    this.countryService.getAllState().subscribe(
      (resp: any) => {
        this.states = resp;
        this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );
      }, (err) => {

      }
    )
  }
  displayState(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  getAllCity() {
    this.countryService.getAllCities().subscribe(
      (resp: any) => {
        this.cities = resp;
        this.filteredOptionCity = this.form.controls.city.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCity(name) : this.cities)
          );
      }, (err) => {

      }
    )
  }
  displayCity(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCity(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  getStateCountry() {
    this.pin_code_id = this.form.value.pincode;
    this.countryService.getCountryStateByPIN(this.pin_code_id).subscribe(
      (data: any) => {
        if (data != undefined && data != null && data.length > 0) {
          this.state_id = data[0].state_id;
          this.countryService.getCountryByState(this.state_id).subscribe(
            (countrydata: any) => {
              this.countries = countrydata;
              const toSelect = this.countries.find(c => c.id == data[0].country_id);
              this.form.get('country').setValue(toSelect);

              this.filteredOptionCountry = this.form.controls.country.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCountry(name) : this.countries)
                );
              this.country_id = data[0].country_id;
              this.countryService.getStateByCountry(this.country_id).subscribe(
                (statedata: any) => {
                  this.states = statedata;
                  const toSelect = this.states.find(c => c.id == data[0].state_id);
                  this.form.get('state').setValue(toSelect);

                  this.filteredOptionState = this.form.controls.state.valueChanges
                    .pipe(
                      startWith(''),
                      map(value => typeof value === 'string' ? value : value.name),
                      map(name => name ? this._filterState(name) : this.states)
                    );

                }, (err) => {

                }
              )
              this.state_id = data[0].state_id;
              this.countryService.getCityByState(this.state_id).subscribe(
                (Citydata: any) => {
                  this.cities = Citydata;
                  //const toSelect = this.states.find(c => c.id == data[0].state_id);
                  // this.form.get('state').setValue(toSelect)
                  this.filteredOptionCity = this.form.controls.city.valueChanges
                    .pipe(
                      startWith(''),
                      map(value => typeof value === 'string' ? value : value.name),
                      map(name => name ? this._filterCity(name) : this.cities)
                    );

                }, (err) => {

                }
              )
            }, (err) => {

            }
          )
        } else {

        }
      }, (err) => {

      }
    )
  }
  onCountryChange(event) {
    this.countries = [];
    this.getAllCountry();
    this.getStatesByCountry(event);
    //this.form.get('pincode').setValue( "");
    this.form.get('state').setValue("");
    this.form.get('city').setValue("");
  }

  getStatesByCountry(state) {
    this.state_id = state.id;
    this.countryService.getStateByCountry(this.state_id).subscribe(
      (statedata: any) => {
        //this.form.reset();
        this.states = statedata;
        // const toSelect = this.states.find(c => c.id == data[0].state_id);
        // this.form.get('state').setValue(toSelect);
        this.filteredOptionState = this.form.controls.state.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterState(name) : this.states)
          );

      }, (err) => {

      }
    )
  }

  onSubmit() {
    if (this.form.valid) {
      let formValues = this.form.value;

      let params = {
        charger_id: this.chargerData == undefined ? 0 : this.chargerData.charger_id,
        user_id: this.localStorage.userId,
        station_name: formValues.station_name,
        provider: formValues.provider,
        model: formValues.model.id,
        lat: formValues.lat,
        lng: formValues.lng,
        charger_serial_no: formValues.char_seri_no,
        address1: formValues.addressLine1,
        address2: formValues.addressLine2,
        PIN: formValues.pincode,
        landmark: formValues.landmark,
        city_id: formValues.city.id,
        state_id: formValues.state.id,
        country_id: formValues.country.id,
        remarks: formValues.remarks,
        image_url: formValues.image,
        status: this.status == true ? 'M' : 'N',
        created_by: this.localStorage.userId,
      }
      this.regCharReqService.createChargerRequest(params).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.snackBar.open(MessageConstant.chargerRequest, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/customerManagement/registerChargerRequest/crud/listMyChargerRequests']);
          } else {
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          }
        },
        (err) => {
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )
    }
    else {
      return
      //this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 3000 });
    }


  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  getCitiesByStates(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCityByState(this.state_id).subscribe(
      (citiesdata: any) => {
        //this.form.reset();
        this.cities = citiesdata;
        // const toSelect = this.states.find(c => c.id == data[0].state_id);
        // this.form.get('state').setValue(toSelect);

        this.filteredOptionCity = this.form.controls.city.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCity(name) : this.cities)
          );

      }, (err) => {

      }
    )
  }
  getCountryByState(stateId) {
    this.state_id = stateId.id;
    this.countryService.getCountryByState(this.state_id).subscribe(
      (countrydata: any) => {
        //this.form.reset();
        this.countries = countrydata;
        const toSelect = this.countries.find(c => c.id == countrydata[0].id);
        this.form.get('country').setValue(toSelect);

        this.filteredOptionCountry = this.form.controls.country.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCountry(name) : this.countries)
          );

      }, (err) => {

      }
    )
  }
  onStateChange(event) {
    this.getCitiesByStates(event);
    this.getCountryByState(event);
    this.form.get('city').setValue("");
    //this.form.get('pincode').setValue("");
  }
  onCityChange(event) {
    //this.form.get('pincode').setValue("");
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }

  getLatLong() {
    let params = {

    }
    const dialogRef = this.dialog.open(MapDialogComponent, {
      width: '700px',
      height: '410px',
      data: params
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result != null || result != undefined) {
        this.form.get('lat').setValue(result[0].lat);
        this.form.get('lng').setValue(result[0].lng);
      } else {
        this.form.get('lat').setValue(this.mLat);
        this.form.get('lng').setValue(this.mLong);
        this.snackBar.open(MessageConstant.customLocationMessage, 'Dismiss', { duration: 5000 });
      }

    });
  }

  getConnectorTypes() {
    this.masterService.getConnectorTypes().subscribe(
      (data: any) => {
        this.connectorTypes = data;
        this.filteredOptionsConnectorType = this.form.controls.model.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterConnectorType(name) : this.connectorTypes)
          );
      }, (err) => {
      }
    )
  }
  private _filterConnectorType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.connectorTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayConnectorType(item: any): string {
    return item && item.name ? item.name : '';
  }

  checkMappedChargerSerialNo() {
    let charSerNo = this.form.value.char_seri_no;
    this.regCharReqService.checkChargerSeriolNoMapping(charSerNo).subscribe(
      (resp: any) => {
        if (resp.status == false) {
          this.flag = true;
          if (resp.data != null || resp.data != undefined) {
            this.chargerData = resp.data[0];
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
            //this.snackBar.open('', 'Dismiss', { duration: 5000 });
          } else {
            this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          }
        } else {
          this.chargerData = resp.data[0];
          this.flag = false;
          this.form.get('char_seri_no').setValue('');
          this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          //this.router.navigate(['/customerManagement/registerChargerRequest/crud/listMyChargerRequests']);
        }
      }, (err) => {

      }
    );
  
  }






}


