import { NgModule } from '@angular/core';
import { SharedModule } from 'app/shared/shared.module';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { RegisterChargerRequestRoutingModule } from './register-charger-request-routing.module';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { RegisterChargerRequestCrudComponent } from './register-charger-request-crud/register-charger-request-crud.component';
import { ApprovedListComponent } from './approved-list/approved-list.component';
import { FuseSharedModule } from '@fuse/shared.module';
import { ViewDialogComponent } from './view-dialog/view-dialog.component';

const COMPONENTS = [RegisterChargerRequestCrudComponent, CreateComponent, ListComponent, ApprovedListComponent];
const COMPONENTS_DYNAMIC = [ActionDialogComponent, ViewDialogComponent];

@NgModule({
    imports: [
        SharedModule,
        RegisterChargerRequestRoutingModule,
        FuseSharedModule,
    ],
    declarations: [COMPONENTS, COMPONENTS_DYNAMIC],
    entryComponents: [COMPONENTS_DYNAMIC]
})

export class RegisterChargerRequestModule { }