
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { RegisterChargerRequestService } from 'app/shared/services/customer-management/register-charger-request.service';


export class ChargerRequest {
  id: number;
  user_id: string;
  user_name: string;
  charger_id: string;
  charger_name: string;
  charger_serial_no: string;
  station_name: string;
  provider: string;
  model: string;
  lat: number;
  lng: number;
  address1: string;
  address2: string;
  PIN: number;
  landmark: string;
  city_name: string;
  state_id: number;
  state_name: string;
  country_id: number;
  country_name: string;
  remarks: string;
  image_url: string;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString: string;
}


@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ListComponent implements OnInit {

  filterEntity: ChargerRequest;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['user_name', 'charger_name', 'charger_serial_no', 'station_name', 'city_name', 'state_name', 'status', 'action'];
  head = [['UserName', 'Charger Name', 'Charger Serial No', 'Station Name', 'City', 'State', 'Status']];
  dataSource: MatTableDataSource<ChargerRequest>;

  chargerReq: ChargerRequest[];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  usernameSearch = false;
  charNameSearch = false;
  serialSearch = false;
  stationSearch = false;
  citySearch = false;
  stateSearch = false;
  statusSearch = false;
  exportedData: any[];
  localStorage: any;


  constructor(private regCharReqService: RegisterChargerRequestService, public dialog: MatDialog, private snackBar: MatSnackBar,
    private cdr: ChangeDetectorRef) {

  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.filterEntity = new ChargerRequest();
    this.filterType = MatTableFilter.ANYWHERE;
    this.getAllModCharRequest();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllModCharRequest() {
    if (this.localStorage.role_name != "END USER") {
      this.regCharReqService.getAllModerateChargerRequest().subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.chargerReq = resp.data;
            if (resp.data.length > 0) {
              for (var i = 0; i < this.chargerReq.length; i++) {
                if (this.chargerReq[i].status == 'A') {
                  this.chargerReq[i].statusString = 'Approved';
                } else {
                  this.chargerReq[i].statusString = 'Moderate';
                }
              }
              this.dataSource = new MatTableDataSource(this.chargerReq);
              this.dataSource.paginator = this.paginator;
              this.dataSource.sort = this.sort;
              this.cdr.detectChanges();
            }else{
              this.snackBar.open(resp.message, 'Dismiss', { duration: 3000 });
            }
            
          } else {
            this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
            return;
          }

        }, (err) => {
        }
      );
    } else {
      this.regCharReqService.getModerateChargerRequestByUserId(this.localStorage.userId).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.chargerReq = resp.data;
            if (resp.data.length > 0) {
              for (var i = 0; i < this.chargerReq.length; i++) {
                if (this.chargerReq[i].status == 'A') {
                  this.chargerReq[i].statusString = 'Approved';
                } else {
                  this.chargerReq[i].statusString = 'Moderate';
                }
                if(this.chargerReq[i].user_name == null ){
                  this.chargerReq[i].user_name = '';
                }
              }
              this.dataSource = new MatTableDataSource(this.chargerReq);
              this.dataSource.paginator = this.paginator;
              this.dataSource.sort = this.sort;
              this.cdr.detectChanges();
            } else {
              this.snackBar.open(resp.message, 'Dismiss', { duration: 3000 })
            }

          } else {
            this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
            return;
          }

        }, (err) => {
        }
      );
    }

  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.regCharReqService.deleteChargerRequest(id, this.localStorage.userId).subscribe(
          (resp: any) => {
            if (resp.status == true) {
              this.getAllModCharRequest();
              this.cdr.detectChanges();
              this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 5000 });
            } else {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
            }

          }, (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
          }
        )
      }
      this.confirmDialogRef = null;
    });

  }

  viewDialog(action, data, buttonAction): void {
    let params = {
      action: action,
      data: data,
      buttonAction: buttonAction
    }
    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1200px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getAllModCharRequest();
    });
  }

  openDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }
    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1200px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });
    dialogRef.afterClosed().subscribe(result => {
      this.getAllModCharRequest();
      this.cdr.detectChanges();
    });
  }

  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();
    if (visibleOption == 'usernameSearch') {
      this.usernameSearch = !this.usernameSearch;
      this.filterEntity.user_name = '';
    }
    if (visibleOption == 'charNameSearch') {
      this.charNameSearch = !this.charNameSearch;
      this.filterEntity.charger_name = '';
    }
    if (visibleOption == 'serialSearch') {
      this.serialSearch = !this.serialSearch;
      this.filterEntity.charger_serial_no = '';
    }
    if (visibleOption == 'stationSearch') {
      this.stationSearch = !this.stationSearch;
      this.filterEntity.station_name = '';
    }
    if (visibleOption == 'citySearch') {
      this.citySearch = !this.citySearch;
      this.filterEntity.city_name = '';
    }
    if (visibleOption == 'stateSearch') {
      this.stateSearch = !this.stateSearch;
      this.filterEntity.state_name = '';
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }
  }
  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var charReq = this.dataSource.filteredData[i];
      var data = {
        'Username': charReq.user_name, 'Charger Name': charReq.charger_name, 'Charger Serial No': charReq.charger_serial_no,
        'Station Name': charReq.station_name, 'City': charReq.city_name, 'State': charReq.state_name,
        'Status': charReq.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'Charger Request List');
    XLSX.writeFile(workBook, 'Charger Request List.xlsx');
    //XLSX.writeFileAsync('OCPP Logs.xlsx',workBook, this.cb)
    //this.preloader.close();
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var charReq = this.dataSource.filteredData[i];

      var data = [charReq.user_name, charReq.charger_name, charReq.charger_serial_no, charReq.station_name, charReq.city_name,
      charReq.state_name, charReq.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Charger Request List Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    // below line for Download PDF document  
    doc.save('Charger Request List.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example
    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;

  }


}

